import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";
import "@fortawesome/fontawesome-free/css/all.min.css";
import {
  MDBCard,
  MDBCardBody,
  MDBCardImage,
  MDBCardTitle,
  MDBCardText,
  MDBRow,
  MDBCol,
} from "mdbreact";

import logo1 from "../assets/information.svg";

// eslint-disable-next-line no-lone-blocks
{
  /*import "@fortawesome/fontawesome-free/css/all.min.css"; */
}

const CardMD1 = () => {
  return (
    <MDBRow>
      <MDBCol md="12">
        <MDBCard cascade className="mdbcard">
          <MDBCardImage
            cascade
            className="img-fluid"
            overlay="white-light"
            hover
            src={""}
          />

          <MDBCardBody cascade>
            <MDBCardTitle className="CardTitleText">
              <img
                className="TitleLogo"
                src={logo1}
                alt="Business Applications"
              />
              Business Applications
            </MDBCardTitle>
            <hr />
            <MDBCardText>
              <div className="mdbcard__text">
                <ul className="mdbcard__ul">
                  <li className="black-text">Oracle EBS</li>
                  <li className="black-text">SAP</li>
                  <li className="pr-2 black-text">
                    ERP for SME's (cloud and on premises)
                  </li>
                  <li className="pr-2 black-text">HCM (HR/PAYROLL)</li>
                </ul>
              </div>
            </MDBCardText>
          </MDBCardBody>
        </MDBCard>
      </MDBCol>
    </MDBRow>
  );
};

export default CardMD1;
