import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";
import "@fortawesome/fontawesome-free/css/all.min.css";
import {
  MDBCard,
  MDBCardBody,
  MDBCardImage,
  MDBCardTitle,
  MDBCardText,
  MDBRow,
  MDBCol,
} from "mdbreact";

import logo2 from "../assets/storage.svg";

// eslint-disable-next-line no-lone-blocks
{
  /*import "@fortawesome/fontawesome-free/css/all.min.css"; */
}

const CardMD2 = () => {
  return (
    <MDBRow>
      <MDBCol md="12">
        <MDBCard cascade className="mdbcard">
          <MDBCardImage
            cascade
            className="img-fluid Card-Image"
            overlay="white-light"
            hover
            src={""}
          />

          <MDBCardBody cascade>
            <MDBCardTitle className="CardTitleText">
              <img className="TitleLogo" src={logo2} alt="Solutions" />
              Solutions
            </MDBCardTitle>
            <hr />
            <MDBCardText>
              <div className="mdbcard__text">
                <ul className="mdbcard__ul">
                  <li className="pr-2 black-text">eCommerce Solutions</li>
                  <li className="pr-2 black-text">IOT</li>
                  <li className="pr-2 black-text">RPA</li>
                </ul>
              </div>
            </MDBCardText>
          </MDBCardBody>
        </MDBCard>
      </MDBCol>
    </MDBRow>
  );
};

export default CardMD2;
