import React from "react";
import styled from "styled-components";
import { FaCommentAlt, FaThumbsUp, FaRegEye } from "react-icons/fa";
import ServiceCard from "../Shared/ServiceCardUI";
const StyledRoot = styled.div`
  padding: 50px 12px;
`;
const StyledContainer = styled.div`
  max-width: 550px;
  width: 100%;
  margin: auto;
`;
const ServiceCards = () => {
  const services = [
    {
      id: "1",
      description: (
        <React.fraction>
          <ul>
            <li>ERP</li>
            <li>Oracle EBS</li>
            <li>SAP</li>
            <li>ERP for SME's (cloud and on premises)</li>
          </ul>
        </React.fraction>
      ),
    },
    {
      id: "2",
      description: (
        <React.fraction>
          <ul>
            <li>HCM (HR/PAYROLL)</li>
            <li>eCommerce Solutions</li>
            <li>Websites</li>
            <li>RPA</li>
          </ul>
        </React.fraction>
      ),
    },
    {
      id: "3",
      description: (
        <React.fraction>
          <ul>
            <li>Infra Management</li>
            <li>Server Upgrades</li>
            <li>Disaster recovery and High availability</li>
            <li>Cloud transformation, Virtualisation</li>
            <li>Digital Transformation</li>
            <li>Database Services: Oracle ad SQL server installations</li>
            <li>Upgrades and tuning.</li>
            <li>
              HA ad Disaster recovery using Oracle Golden Gate, Data Gaurd,
              RMAN, RAC
            </li>
            <li>Huma Resource Augmentation</li>
          </ul>
        </React.fraction>
      ),
    },
  ];

  const buttons = [
    {
      id: "1",
      label: (
        <>
          <FaCommentAlt /> 0 Comments
        </>
      ),
    },
    {
      id: "2",
      label: (
        <>
          <FaThumbsUp /> 242 Likes
        </>
      ),
    },
    {
      id: "3",
      label: (
        <>
          <FaRegEye /> 187288 Views
        </>
      ),
    },
  ];
  return (
    <StyledRoot>
      <StyledContainer>
        <ServiceCard
          title="The Benefits of Green Apples"
          services={services}
          actions={buttons}
        />
      </StyledContainer>
    </StyledRoot>
  );
};
export default ServiceCards;
