import React, { Component } from "react";

import CardMD1 from "../Shared/CardMD1";
import CardMD2 from "../Shared/CardMD2";
import CardMD3 from "../Shared/CardMD3";
import CardMD4 from "../Shared/CardMD4";

import "../Shared/Cards.css";

class Cards extends Component {
  render() {
    return (
      <React.Fragment>
        <div className="container-fluid    d-block d-sm-block d-md-flex d-lg-flex d-xl-flex align-items-stretch">
          <div className="row">
            <div className="row-eq-height col-md-3 w-25">
              <CardMD1></CardMD1>
            </div>
            <div className="row-eq-height col-md-3 w-25">
              <CardMD2></CardMD2>
            </div>
            <div className="row-eq-height col-md-3 w-25">
              <CardMD3></CardMD3>
            </div>
            <div className="row-eq-height col-md-3 w-25">
              <CardMD4></CardMD4>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Cards;
