import React from "react";
import { MDBJumbotron, MDBContainer, MDBCol } from "mdbreact";
import Slogan from "../assets/slogan.png";
import GoldenRiver from "../assets/Goldenwave.jpg";
import "../Shared/Header.css";

const JumbotronHeader = () => {
  return (
    <MDBJumbotron
      fluid
      style={{
        padding: 0,
        backgroundImage: `url(${GoldenRiver})`,
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
      }}
    >
      <MDBCol
        className=" text-white text-center py-5 px-4 my-5"
        style={{
          textColor: "red",
          padding: 0,
        }}
      >
        <MDBContainer className="container-fluid">
          <h2 className="CompanyName">Gold River Technologies</h2>
        </MDBContainer>
        <MDBContainer className="Slogan-Image">
          <img src={Slogan} alt="Listen,Understand,Think,Solve" />
        </MDBContainer>
      </MDBCol>
    </MDBJumbotron>
  );
};

export default JumbotronHeader;
