import React from 'react'
import styled, { withTheme } from 'styled-components'
const StyledContainer = styled.div`
  border: 1px;
  padding: 25px 12px 18px;
  background: ${props => `linear-gradient(
    45deg,0 , 0.5
  )`};
`

const StyledPhoto = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  border: 1px};
`

const Title = styled.h2`
  color: #fff;
  font-weight: 300;
  @media (max-width: 500px) {
    font-size: 1rem;
  }
`
const Date = styled.div`
  color: #ccc;
  font-weight: 300;
  margin: 6px 0;
  @media (max-width: 500px) {
    font-size: 0.8rem;
  }
`
const Description = styled.p`
  color: #fff;
  font-weight: 300;
  @media (max-width: 500px) {
    font-size: 0.75rem;
  }
`

const Services = styled.div`
  color: #333;
  display: flex;
  align-items: center;
  svg {
    transform: translateY(2px);
    margin-right: 5px;
  }
  @media (max-width: 500px) {
    flex-direction: column;
    
    }
  }
`

const Actions = styled.div`
  color: #333;
  display: flex;
  align-items: center;

  @media (max-width: 500px) {
    flex-direction: column;
  }
`
const Action = styled.button`
  height: 30px;
  width: 100px;
  margin: 0 5px;
  padding: 8px 14px;
  background: rgba(155, 155, 155, 0.2);
  color: #fff;
  cursor: pointer;
  border: 1px solid #fff;
  outline: 0;
  foreground: black;
  font-weight: 300;
  :hover {
    opacity: 0.8;
  }
  :active {
    background: rgba(155, 155, 155, 0.2);
  }
`
const ServiceCard = ({ title, services, comments, likes, views, actions }) => (
  <StyledContainer>
    <StyledPhoto src='https://s3-us-west-1.amazonaws.com/welcome_apples/posts/1_card/apple.jpg' />
    <Title>{title}</Title>

    {actions.map((id, label) => (
      <Actions key={id}>
        <Action label={label}></Action>
      </Actions>
    ))}
  </StyledContainer>
)
export default withTheme(ServiceCard)
