import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";
import "@fortawesome/fontawesome-free/css/all.min.css";
import {
  MDBCard,
  MDBCardBody,
  MDBCardImage,
  MDBCardTitle,
  MDBCardText,
  MDBRow,
  MDBCol,
} from "mdbreact";

import logo3 from "../assets/data.svg";

// eslint-disable-next-line no-lone-blocks
{
  /*import "@fortawesome/fontawesome-free/css/all.min.css"; */
}

const CardMD4 = () => {
  return (
    <MDBRow>
      <MDBCol md="12">
        <MDBCard cascade className="mdbcard">
          <MDBCardImage
            cascade
            className="img-fluid Card-Image"
            overlay="white-light"
            hover
            src={""}
          />

          <MDBCardBody cascade>
            <MDBCardTitle className="CardTitleText">
              <img className="TitleLogo" src={logo3} alt="Database Services" />
              Database Services
            </MDBCardTitle>
            <hr />
            <MDBCardText>
              <div className="mdbcard__text">
                <ul className="mdbcard__ul">
                  <li className="pr-2 black-text">
                    Oracle and SQL server installations
                  </li>
                  <li className="pr-2 black-text">Upgrades and tuning</li>
                  <li className="pr-2 black-text">
                    HA and Disaster recovery using Oracle Golden Gate, Data
                    Guard, RMAN, RAC
                  </li>
                </ul>
              </div>
            </MDBCardText>
          </MDBCardBody>
        </MDBCard>
      </MDBCol>
    </MDBRow>
  );
};

export default CardMD4;
